header {
  z-index: 100;
}
header .header_area {
  border-bottom: 1px solid #eee;
}
header .header_area .inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 0;
}
header .header_area .inner .logo {
  display: flex;
  align-items: end;
}
header .header_area .inner .logo h1 {
  width: 117px;
}
header .header_area .inner .logo h1 img {
  width: 100%;
}
header .header_area .inner .logo h1 + span {
  font-size: 16px;
  color: #222;
  letter-spacing: 0.5em;
}
header .header_area .inner .info_menu {
  display: flex;
  align-items: center;
}
header .header_area .inner .info_menu ul {
