  width: 36px;
  margin-left: 44px;
}
header .header_area .inner .info_menu ul li a {
  position: relative;
  display: block;
  padding-bottom: 19px;
}
header .header_area .inner .info_menu ul li a img {
  width: 100%;
}
header .header_area .inner .info_menu ul li a span {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  font-size: 13px;
  color: #666;
  white-space: nowrap;
}
header .nav_area {
  position: relative;
  background-color: #fff;
  z-index: 100;
}
header .nav_area > .inner {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 13px 0;
}
header .nav_area > .inner .nav {
  display: flex;
  justify-content: center;
}
header .nav_area > .inner .nav li a {
