  margin-right: 40px;
  font-size: 16px;
  font-weight: 500;
  color: #000;
}
header .nav_area > .inner .nav li.logo {
  display: none;
}
header .nav_area > .inner .search_area {
  position: relative;
  width: 243px;
  height: 24px;
  padding: 0 10px;
  border-left: 1px solid #cacaca;
  border-right: 1px solid #cacaca;
}
header .nav_area > .inner .search_area input {
  height: 22px;
  line-height: 22px;
  border: 1px solid transparent;
  vertical-align: top;
}
header .nav_area > .inner .search_area input:focus {
  border-color: #000;
}
header .nav_area > .inner .search_area span {
  position: absolute;
  top: 0;
  right: 10px;
  width: 26px;
  cursor: pointer;
  background: url("http://localhost:3000/img/search.png") no-repeat 50% 50%;
  background-size: 26px;
  overflow: hidden;
  text-indent: -999em;
}
header .nav_area .nav_detail,
header .nav_area .nav_detail_mo {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background: #fff;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
  z-index: 100;
  border-bottom: 2px solid #fb4357;
}
header .nav_area .nav_detail .inner,
header .nav_area .nav_detail_mo .inner {
  display: flex;
}
header .nav_area .nav_detail .inner ul,
header .nav_area .nav_detail_mo .inner ul {
  flex: 1;
  border-right: 1px solid #ccc;
  padding: 25px 0;
  padding-left: 10px;
}
header .nav_area .nav_detail .inner ul li a,
header .nav_area .nav_detail_mo .inner ul li a {
  color: #555;
  font-size: 14px;
}
header .nav_area .nav_detail .inner ul li:first-child,
header .nav_area .nav_detail_mo .inner ul li:first-child {
  margin-bottom: 10px;
}
header .nav_area .nav_detail .inner ul li:first-child a,
header .nav_area .nav_detail_mo .inner ul li:first-child a {
  font-weight: 500;
  color: #000;
  font-size: 16px;
}
header .nav_area .nav_detail .inner ul:first-child,
header .nav_area .nav_detail_mo .inner ul:first-child {
  padding-left: 0;
}
header .nav_area .nav_detail .inner ul:last-child,
header .nav_area .nav_detail_mo .inner ul:last-child {
  border: none;
}
header .nav_area.active {
  display: block;
}
header .nav_area:after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #fb4357;
}
header .nav_area.fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-image: linear-gradient(to right, rgb(215, 67, 87), rgb(241, 79, 58) 59%, rgb(239, 100, 47));
}
header .nav_area.fixed > .inner .nav .logo {
  width: 80px;
  display: block;
}
header .nav_area.fixed > .inner .nav .logo img {
  position: relative;
  top: -2px;
  height: 24px;
  vertical-align: middle;
}
header .nav_area.fixed > .inner .nav a {
  color: #fff !important;
}
header .nav_area.fixed > .inner .search_area span {
  background: url("http://localhost:3000/img/search_w.png") no-repeat 50% 50%;
  background-size: 26px;
}

footer {
