.q_nav_area {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 100;
  opacity: 0;
  transition: all 1s;
}
.q_nav_area .btn_go {
  position: absolute;
  top: 0;
  right: -50px;
