  box-shadow: 1px 3px 6px 0 rgba(0, 0, 0, 0.3);
  border-radius: 25px;
  color: #fff;
  font-weight: 500;
  text-align: center;
  line-height: 50px;
  opacity: 0;
  transition: all 1s;
}
.q_nav_area .btn_top {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
