.wrap .contents_area .notice_area .notice_box .info_list {
  flex: 1;
  padding: 24px 26px 20px 30px;
}
.wrap .contents_area .notice_area .notice_box .info_list .top_noti {
  display: flex;
}
.wrap .contents_area .notice_area .notice_box .info_list .top_noti strong {
  font-size: 16px;
  width: 60px;
}
.wrap .contents_area .notice_area .notice_box .info_list .top_noti div {
  overflow: hidden;
  max-width: 300px;
  padding-left: 28px;
  font-size: 14px;
  color: #222;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.wrap .contents_area .notice_area .notice_box .info_list .top_noti a {
  display: inline-block;
  padding: 2px 15px 3px;
  font-weight: 400;
  font-size: 12px;
  color: #666;
  line-height: 1.417em;
  border: 1px solid #f4f4f4;
  border-radius: 11px;
  margin-left: auto;
}
.wrap .contents_area .notice_area .notice_box .info_list dl {
  display: flex;
  margin: 20px 0;
}
.wrap .contents_area .notice_area .notice_box .info_list dl dt {
  font-size: 16px;
  width: 60px;
}
.wrap .contents_area .notice_area .notice_box .info_list dl dd {
  padding-left: 28px;
  font-size: 14px;
}
.wrap .contents_area .notice_area .notice_box .info_list .btns a {
  display: inline-block;
  padding: 7px 14px;
  font-size: 14px;
  color: #222;
  line-height: 1.429em;
  background-color: #f6f6f6;
  border-radius: 5px;
}
.wrap .contents_area .notice_area .notice_box .qr {
  width: 222px;
  margin-top: 5px;
  border-left: 1px solid #e4e4e4;
  font-size: 12px;
  color: #222;
  text-align: center;
}
.wrap .contents_area .notice_area .notice_box .qr div:first-child {
  padding: 24px 0 29px;
}
.wrap .contents_area .notice_area .notice_box .qr div:first-child strong {
  display: block;
}
.wrap .contents_area .notice_area .notice_box .qr > img {
  width: 60px;
  height: 60px;
}
.wrap .contents_area .notice_area .notice_box .qr > span {
  display: block;
  margin-top: 24px;
}
.wrap .contents_area .notice_area .ban_area {
  width: 211px;
  height: 238px;
  border-radius: 10px;
  overflow: hidden;
}
.wrap .contents_area .notice_area .ban_area img {
  width: 100%;
}